@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.detail_title{color:#000;margin-top:15px}.btn_red,.btn_orange{background-color:red;color:white;text-align:center;border:0px;padding-left:16px;padding-right:16px;padding-top:8px;padding-bottom:8px;border-radius:5px;margin-left:2px;margin-right:2px}.btn_orange{background-color:#ff7f00}.my_alret,.my_alret_red{background-color:#eeeeee;padding:15px;border-radius:5px;max-width:500px;margin:auto}.my_alret_red{background-color:red}.my_alret p,.my_alret_red p{margin-bottom:0}

.nav_title {
  font-family: 'Noto Sans KR', sans-serif;
}

.App {
  text-align: center;
}


.jumbo_bg {
  background-image: url(/jumbo_bg.png);
  background-position : center;
  color: white;
}

.row {
  margin-top: 15px;
}
