/* deail.js만을 위한 css */

$main_color: black;
$alret_color : #eeeeee;

.detail_title {
    color : $main_color;
    margin-top: 15px;
  } 

.btn_red {
  background-color:red;
  color: white;
  text-align: center;
  border: 0px;
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 5px;
  margin-left: 2px;
  margin-right: 2px;
}

.btn_orange {
  @extend.btn_red;
  background-color: #ff7f00;
}

