.my_alret {
  background-color: #eeeeee;
  padding: 15px;
  border-radius: 5px;
  max-width : 500px;
  margin: auto;
}

//@extend.클래스 이름 -> 이렇게 하면 해당 클래스의 모든 값이 복붙이 된다.
.my_alret_red {
  @extend.my_alret;
  background-color: red;
}

.my_alret p{
  margin-bottom : 0;
}